<template>
<!-- 活动 -->
	<div class="activity">
		<!-- 公告 -->
		<div class="affiche-box">
			<div class="affiche">公告</div>
			<van-swipe
				vertical
				:show-indicators="false"
				:style="{ height: '40px' }"
				autoplay="3000"
			>
				<van-swipe-item v-for="(item, index) in afficheData" :key="index">
					<div class="affiche-content">
						<div class="nickname">{{ item.nickname }}</div>
						<div class="text">刚参加了活动</div>
						<div class="name">{{ item.name }}</div>
					</div>
				</van-swipe-item>
			</van-swipe>
		</div>

		<!-- 进行中活动 -->
		<div class="ongoing">
			<div class="title">热门活动上线中</div>
			<div class="ongoing-item" v-for="(item, index) in ongoing" :key="index">
				<div class="img-box">
					<img class="auto-img" :src="item.image" alt="" />
				</div>
				<div class="name">
					<div class="left">{{ item.name }}</div>
					<div class="right">立即参与<van-icon name="arrow" /></div>
				</div>
			</div>
		</div>

		<!-- 已结束活动 -->
		<div class="ended">
			<div class="title">往期回顾</div>
      <div class="ended-item" v-for="(item, index) in ended" :key="index">
        <div class="banner">
          <img
            class="auto-img"
            :src="item.image"
            alt=""
          />
        </div>
        <div class="name">
          <div class="left">{{ item.name }}</div>
          <div class="right">查看详情<van-icon name="arrow" /></div>
        </div>
      </div>
			
		</div>
	</div>
</template>
<script>
export default {
	name: "Activity",
	data() {
		return {
			offset: 0,
			limit: 20,
			// 公告
			afficheData: [],
			// 进行中活动
			ongoing: [],
      // 已结束活动
      ended: []
		};
	},
	created() {
		this.getActiveData();
	},
	methods: {
		getActiveData() {
			this.axios({
				method: "get",
				url:
					"https://apis.netstart.cn/douguo/home/events/getlists?offset=" +
					this.offset +
					"limit=" +
					this.limit,
			})
				.then((result) => {
					
					if (result.status === 200) {
						this.afficheData = result.data.data.banner;
						this.ongoing = result.data.data.ongoing.events;
						this.ended = result.data.data.ended.events;
						
					}
				})
				.catch((err) => {
					
				});
		},
	},
};
</script>

<style lang="less" scoped>
/deep/ .van-swipe-item {
	height: 40px;
}
/deep/ .van-swipe {
	margin-left: 0;
	width: 300px;
}
.activity {
	padding-top: 100px;
	padding-bottom: 50px;
	// 公告
	.affiche-box {
		display: flex;
		margin: 10px;
		height: 40px;
		background-color: #f6f6f6;
		align-items: center;
		.affiche {
			height: 18px;
			width: 30px;
			font-size: 14px;
			font-weight: bold;
			background-color: #ffc937;
			padding: 2px;
			border-radius: 8px;
			margin-left: 10px;
		}
		.affiche-content {
			display: flex;
			margin-left: 10px;
			line-height: 40px;
			overflow: hidden;
			font-size: 12px;
			.nickname {
				height: 40px;
				font-weight: bold;
				text-overflow: ellipsis;
			}
			.text {
				height: 40px;
			}
			.name {
				font-weight: bold;
				height: 40px;
				overflow: hidden;
				text-overflow: ellipsis;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 1;
				-webkit-box-orient: vertical;
				overflow: hidden;
			}
		}
	}
	// 进行中活动
	.ongoing {
		padding: 10px;
		.title {
			font-size: 20px;
			font-weight: bold;
			margin-bottom: 10px;
		}
		.ongoing-item {
			margin-bottom: 10px;
			.img-box {
				width: calc(100% -20px);
				border-radius: 5px;
				margin-bottom: 10px;
				overflow: hidden;
			}
			.name {
				display: flex;
				align-items: center;
				font-size: 14px;
				.left {
					width: 78%;
					padding-right: 5px;
					font-weight: bold;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					line-height: 24px;
				}
				.right {
					width: 22%;
					display: flex;
					align-items: center;
					padding-left: 10px;
					font-size: 14px;
					padding-right: 0;
					color: #4f9593;
				}
			}
		}
	}

	.ended {
		padding: 10px;
		.title {
			font-size: 20px;
			font-weight: bold;
			margin-bottom: 10px;
		}
    .ended-item {
      margin-bottom: 10px;
      .banner {
			width: 100%;
			border-radius: 5px;
			overflow: hidden;
		}
		.name {
			display: flex;
			align-items: center;
			font-size: 14px;
			.left {
				width: 78%;
				padding-right: 5px;
				font-weight: bold;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
				line-height: 24px;
			}
			.right {
				width: 22%;
				display: flex;
				align-items: center;
				padding-left: 10px;
				font-size: 14px;
				padding-right: 0;
				color: #4f9593;
			}
		}
    }
		
	}
}
</style>
